@import url(https://fonts.googleapis.com/css2?family=Lato:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background: linear-gradient(#ccf7ff 0%, #0198b3 200%);
    margin: 0;
}

.header {
    background-image: url(/reloadly-react-promotion-tracker/static/media/image.68103210.svg);
    display: flex;
    justify-content: center;
}

.instructions {
    margin-top: 200px;
    padding: 0 0 30px 0;
    display: flex;
    justify-content: center;
    text-align: center;
    flex-wrap: wrap;
    width: 35vw;
    font-family: 'Lato', sans-serif;
    font-size: large;
    border: none;
}

.instructions h1 {
    margin-bottom: 20px;
}

.instructions p {
    margin: 20px 0 20px 0;
}

.input_instructions {
    display: flex;
    justify-content: space-around;
    width: 20vw;
    margin-top: 50px;

}

.btn {
    margin-left: 20px;
    height: 50px;
    width: 100px;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: black;
    font-size: 14px;
    text-align: center;
    flex: 1 1 auto;
    transition: 1s;
    background-size: 200% auto;
    background-image: linear-gradient(to right, #b4fdee 0%, #18a1b4 100%);
}

input Button {
    border: none;
    border-radius: 7px;
}

.response {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 70vw;
    margin: auto;
    font-family: 'Lato', sans-serif;
    letter-spacing: 1.5px;
}

.card-body {
    background-color: #d6f3ff;
}

.card {
    margin: 20px 0 20px 0;
    padding: 30px 0 30px 0;
    cursor: pointer; 
}

